                @change="change"
              >
                <div class="img-wrap" slot="top">
                  <img class="" src="~@/assets/tagbg.png" alt="">
                  <div class="img-wrap__title">ÊúÄÊÑüÂÖ¥Ë∂£ÁöÑÊ†èÁõÆ</div>
                  <div class="img-wrap__subtitle">ÂÆöÂà∂‰∏ìÂ±û‰∫éÊàëÁöÑËØæÁ®ãÊ†èÁõÆ</div>
                </div>

                <div class="footer" slot="footer">
                  <a href="#">ËøõÂÖ•È¶ñÈ°µ</a>
