<script>
export default {
  data () {
    return {}
  },
  mounted () {
    // ÂΩ©Â∏¶
    // ÂàùÂßãÂåñÂèòÈáè
    let c = document.getElementsByTagName('canvas')[0]
    let x = c.getContext('2d')
    let pr = window.devicePixelRatio || 1 // ËÆæÁΩÆÂÉèÁ¥†ÊØî
    let w = window.innerWidth
    let h = window.innerHeight
    let f = 90
    let q
    let m = Math
    let r = 0
    let u = m.PI * 2 // Math.PI*2
    let v = m.cos // ‰ΩôÂº¶ÂÄº
    let z = m.random // ÈöèÊú∫Êï∞ÂÄº
    c.width = w * pr // ËÆæÁΩÆ Canvas ÂÆΩÂ∫¶‰∏∫ÂèØËßÜÁ™óÂè£ÁöÑÂÆΩÂ∫¶*ÂÉèÁ¥†ÊØî
    c.height = h * pr // ËÆæÁΩÆ Canvas È´òÂ∫¶‰∏∫ÂèØËßÜÁ™óÂè£ÁöÑÈ´òÂ∫¶*ÂÉèÁ¥†ÊØî
    x.scale(pr, pr) // Áº©Êîæ Canvas pr = 1 = 100%
    x.globalAlpha = 0.6 // ËÆæÁΩÆ Canvas ÈÄèÊòéÂ∫¶
    // Âæ™ÁéØÂáΩÊï∞
    function i () {
      x.clearRect(0, 0, w, h) // Ê∏ÖÁ©∫Áü©ÂΩ¢
      // Êï∞ÁªÑÂèòÈáèÂØπË±°
      q = [{
        x: 0,
        y: h * 0.7 + f
      }, {
        x: 0,
        y: h * 0.7 - f
      }]
      // Âæ™ÁéØÔºå0 < ÂèØËßÜÁ™óÂè£ÂÆΩÂ∫¶+90
      while (q[1].x < w + f) {
        d(q[0], q[1]) // ÁªòÂà∂ÂáΩÊï∞‰º†ÂèÇ
      }
    }
    // ÁªòÂà∂ÂáΩÊï∞‰º†ÂèÇÔºåi Âíå j ÂùáÊúâ‰∏Ä‰∏™ x ‰∏é y Êï∞ÂÄºÂØπË±°
    function d (i, j) {
      x.beginPath() // ÂºÄÂßãË∑ØÂæÑ
      x.moveTo(i.x, i.y) // ÁßªÂä®Ë∑ØÂæÑ
      x.lineTo(j.x, j.y) // ÂàõÂª∫Á∫øÊù°
      var k = j.x + (z() * 2 - 0.25) * f // ÈáçÊñ∞ÂÆö‰πâÊï∞ÁªÑÂØπË±° q[1] ÁöÑÂèòÈáèÂØπË±°Ôºåz() ÈöèÊú∫Êï∞ÂÄº
      var n = y(j.y) // Â∞Ü y() ‰º†ÂèÇÂáΩÊï∞ËµãÂÄºÁªô n
      x.lineTo(k, n) // ÂàõÂª∫Á∫øÊù°
      x.closePath() // ÂÖ≥Èó≠Ë∑ØÂæÑ
      r -= u / -50
      // Â°´ÂÖÖÈ¢úËâ≤ÔºåtoString(16)ËΩ¨Êç¢‰∏∫16ËøõÂà∂È¢úËâ≤ÂÄº
      // ÂÖ≥‰∫éÔºà<< ‰∏é |Ôºâ‰ΩçËøêÁÆóÊìç‰ΩúÂèÇËÄÉÔºöhttp://www.w3school.com.cn/js/pro_js_operators_bitwise.asp
      x.fillStyle = '#' + (v(r) * 127 + 128 << 16 | v(r + u / 3) * 127 + 128 << 8 | v(r + u / 3 * 2) * 127 + 128).toString(
        16)
      x.fill()
      // ÈáçÊñ∞ÂÆö‰πâÊï∞ÁªÑÂØπË±°ÔºåÂ¢ûÂä†Êó†ÈôêÂèØÂèòÊÄß
      q[0] = q[1]
      q[1] = {
        x: k,
        y: n
      }
    }
    // ‰º†ÂèÇÂáΩÊï∞ return ËøîÂõûÊï∞ÂÄº
    function y (p) {
      var t = p + (z() * 2 - 1.1) * f // z() ÈöèÊú∫Êï∞ÂÄº
      return (t > h || t < 0) ? y(p) : t // Âà§Êñ≠ÂêéËøîÂõû‰∏çÂêåÊï∞ÂÄºÂêéÂ¢ûÂä†‰∫ÜÊõ¥Â§öÁöÑÂèØÂèòÊÄß
    }
    // ÁÇπÂáª‰∏éËß¶Êë∏Êó∂ÊâßË°å i() ÂáΩÊï∞
    document.onclick = i
    i()
  }
}
</script>
